
/**
 * HEADER LAYOUT
 */
.site-header{
	position: relative;
	z-index: 2;
	color: #d7c8b5;
	display: grid;
	//grid-auto-columns: 55% 30%;
	transition: all .3s ease-in-out;
	//background-color: rgba($blackT, .85);
	background-image: linear-gradient(to right, $blackT 1%, rgba($blackT, .85) 10%);
	grid-gap: 1px;
	padding-bottom: 2rem;

	@media (max-width: ($small - 1px)) {
		grid-template-columns: auto 4.63em 4.63em;
	}
	@media (max-width: ($medium - 1px)) {
		grid-template-columns: auto 15vw 15vw;
	}

	@media (min-width: $medium) {
		grid-auto-columns: auto 33% 20%;
		padding-bottom: 0;
		padding: 2px 0 0;
	}
	@media (min-width: $wide-large) {
		grid-auto-columns: auto 33% 25%;
	}
	@media (min-width: $wide-large) {
		grid-auto-columns: auto 460px 300px;
	}

	*{
		@include clear-marges();
	}

}

.site-branding{
	grid-column: 1 / 3;
	order:1;
	align-self: flex-end;

	@media (min-width: $large) {
		grid-column: 1 / 2 ;
		order: 0;
	}
	h1,h2,h3,p,a{
		margin: 0; padding: 0;
	}

	.site-title{
		position: relative;
		bottom: -10px;
		left: 30px;
	}

	.site-title a{
		display: block;
		max-width: 400px;
		padding: 0 $spacer-small;
		@media (min-width: $medium) {
			padding: 0 $spacer-medium;
		}
	}
}
.header-info{
	display: grid;
	grid-template-columns: 100%;
	grid-gap: 1rem;
	grid-column: 1 / 2 ;
	padding: $spacer-small;
	@media (min-width: $large) {
		grid-template-columns: 55% 45%;
		grid-column: 2 / 3 ;
		padding: $spacer-medium;
	}

	.h5-like{
		display: none;
		@media (min-width: $medium) { display: block; }
	}
}
.header-phone{
	grid-column: 2 / 3 ;
	padding: $spacer-tiny;
	@media (min-width: $large) {
		max-width: 100%;
		grid-column: 3 / 4 ;
		padding: $spacer-medium;
	}

	.coord-phone{
		display: none;
		@media (min-width: $medium) { display: inline-block; }
	}
}


 /**
 * DESIGN
 */

 .site-branding{
	 h1,p,a{
		@include font-size(h1);
		color: $gray-100;
		font-weight: $weight-light;
		font-family: $font-family-headings;
		text-decoration: none;
		line-height: 1em;
	 }
	 .site-description{
		 @include font-size(h4);
		 font-weight: $weight-bold;
		 font-weight: bold;
		 word-spacing: 0.18em;
	 }
	 .site-title a{
		background: transparent url(../img/logo-MaudCHENUT-BRIOTET.svg) no-repeat center center;
		background-size: 100% auto;
		*{ visibility: hidden; }
	 }
 }

.header-info,
.header-phone{
	background-color: $redTC;
	color: rgba($gray-100, .75);
}
.header-phone{
	display: flex;
	align-items: center;
	justify-content: center;
	text-decoration: none;
	color: rgba($gray-100, .9);

	&:hover, &:focus, &:visited{ text-decoration: none; }
	&:hover, &:focus{ color: $gray-100; }

	@include font-size(h3);
	.flaticon-smartphone{
		display: inline-block;
		width: $spacer-large;
		height: $spacer-large;
		@media (min-width: $medium) { margin-right: $spacer-medium;}
		@include font-size(h1);
		vertical-align: middle; text-align: center;
		line-height: $spacer-large;
		transform: scale(1.3);
	}
	.coord-phone{
		font-weight: bold;
		font-size: 2.2rem;
	}
}
.header-info{
	background-color: rgba( $redTC, .8);
	.h5-like{
		font-style: italic;
	}
}
